	border-radius: 12px;
	border-color: rgb(255, 255, 255);
	transition: ease 0.5s;
	border-style: solid;
	padding: 7px 7px 7px 5px;
	
}

.dropdown-content-cog a:hover {
	background-color: rgb(55, 171, 248);
	color: white;
}

.dropdown-content-cog a {
	color: black;
	padding: 12px 16px;
	text-decoration: none;
	display: block;
	width: 144px;
}

.dropdown:hover .dropdown-content-cog {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
